// axios 
import axios from 'axios'

// url
axios.defaults.baseURL = 'http://api.duyiedu.com'
// 
axios.interceptors.request.use(function (config) {
  // 
  config.params = {
    ...config.params,
    appkey: 'QQ876496731_1546865529631'
  }
  return config
}, function (error) {
  // 
  return Promise.reject(error)
})
export default axios
