<template>
  <div class="m-menu">
    <dl class="nav" @mouseleave="menuLeave">
      <dt></dt>
      <dd  v-for="item in navList" :key="item.name" @mouseenter="menuEnter(item)">
        <i :class="item.icon"></i>
        {{item.name}}
        <span class="arrow"></span>
      </dd>
    </dl>
    <div v-if="curDetail" class="detail" @mouseenter="detailEnter" @mouseleave="datailLeave">
      <template v-for="(item, index) in curDetail">
        <h4 :key="index">{{item.title}}</h4>
        <span v-for="(v, i) in item.label" :key="v + '_' + i">{{v}}</span>
      </template>
    </div>
  </div>
</template>

<script>
import api from '../../api/index.js'
export default {
  data () {
    return {
      curDetail: null,
      navList: [{
        name: '',
        icon: 'food',
        children: [
          {
            title: '',
            label: ['', '', '', '', '']
          }
        ]
      }, {
        name: '',
        icon: 'takeout'
      }, {
        name: '',
        icon: 'hotel'
      }, {
        name: '',
        icon: 'homestay'
      }, {
        name: 'movie',
        icon: 'food'
      }, {
        name: '/',
        icon: 'airport'
      }, {
        name: '/KTV',
        icon: 'ktv'
      }, {
        name: '',
        icon: 'life'
      }, {
        name: '//',
        icon: 'hair'
      }, {
        name: '//',
        icon: 'marry'
      }, {
        name: '//',
        icon: 'offspring'
      }, {
        name: '/',
        icon: 'sport'
      }, {
        name: '//',
        icon: 'furniture'
      }, {
        name: '/',
        icon: 'study'
      }, {
        name: '//',
        icon: 'health'
      }, {
        name: '/',
        icon: 'bar'
      }]
    }
  },
  created () {
    api.getMenuList().then(res => {
      // let { data } = res.data
      // console.log(data)
    })
  },
  methods: {
    menuEnter (item) {
      this.curDetail = item.children
    },
    menuLeave () {
      let self = this
      this.timer = setTimeout(() => {
        self.curDetail = null
      }, 200)
    },
    detailEnter () {
      clearTimeout(this.timer)
    },
    datailLeave () {
      this.curDetail = null
    }
  }
}
</script>
