<template>
  <div class="page-login">
    <div class="login-header">
      <a class="logo"></a>
    </div>
    <div class="login-panel">
      <div class="banner">
        <img src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" alt="">
      </div>
      <div class="form">
        <h4 class="tips" v-if="error">{{error}}</h4>
        <p>
          <span class="loginTitle"></span>
        </p>
        <el-input
          :class="{error: error && !userName}"
          placeholder="//"
          prefix-icon="profile"
          v-model="userName">
        </el-input>
        <el-input
          :class="{error: error && !userName}"
          placeholder=""
          prefix-icon="passworld"
          type="password"
          v-model="passWorld">
        </el-input>
        <div class="foot">
          <router-link :to="{name: 'forget'}"></router-link>
        </div>
        <el-button class="btn-login" type="success" @click="submit"></el-button>
        <p class="sing-up-guide">
          
          <router-link :to="{name: 'register'}" target="_top"></router-link>
        </p>
        <div class="oauth-wrapper">
          <h3 class="title-wrapper">
            <span class="title"></span>
          </h3>
          <div class="oauth cf">
            <a
              class="oauth__link oauth__link--qq"
              href=""
              target="_blank"
            ></a>
            <a
              class="oauth__link oauth__link--weibo"
              href=""
              target="_blank"
            ></a>
          </div>
        </div>
      </div>
    </div>
    <footer>
      <ul>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
        <li><a href="#"></a></li>
      </ul>
      <p>2019  meituan.com ICP070791 11010502025545</p>
    </footer>
  </div>
</template>

<script>
import api from '../../api/index.js'
export default {
  data () {
    return {
      userName: '',
      passWorld: '',
      error: ''
    }
  },
  methods: {
    submit () {
      if (!this.userName) {
        this.error = ''
        return false
      }
      if (!this.passWorld) {
        this.error = ''
        return false
      }
      this.error = ''
      api.login({
        userName: this.userName,
        password: this.passWorld
      }).then(res => {
        if (res.data.status === 'success') {
          this.$router.push({ name: 'index' })
          this.$store.commit('setUserName', this.userName)
        } else {
          this.error = res.data.msg
        }
      })
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/login/index.scss';
</style>
